.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container{

  margin-top: 30px;
  padding-top: 30px;
 background: linear-gradient(130deg,rgb(81, 126, 150),rgb(48, 110, 155));
  
 padding-left: 200px;
 padding-right:200px;
 margin-left: 100px;
 margin-right: 100px;
 border-radius: 25px;
}

.btn{
  border-radius: 15px;
}
.form-control{
  border-radius: 15px;
}
.details{
  margin-left: 0px;
  padding-left:0px;
  list-style-type: none;
}
